import * as React from 'react';
import {
    Grid,
    // FormControl, 
    InputLabel,
    Typography,
    Link,
    Button,
    Checkbox
    // TextareaAutosize,
} from '@material-ui/core';
import { ReactTable } from 'Components/UI/TableComponent/ReactTable';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { BasicTextField, BasicSelect } from 'Components/UI';
import { KeyboardDatePicker } from '@material-ui/pickers';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';
// import BasicSelect from 'Components/UI/BasicSelect'
import moment from 'moment';
import { RolesRbacData } from '../IRolesPage';
export interface IPoliciesProps {
    rows?: any,
    viewOnly: boolean,
    classes: any,
    mode?: 'edit' | 'view' | 'add' | string,
    moduleName?: string,
    rbacList: any,
    onChangeModuleName?: (name) => void,
    onUpdateChange: (data) => void,
    roleId?: string,
    onNavigateSideMenu?: (name) => void,
}

export const Policies: React.FC<IPoliciesProps> = (props: IPoliciesProps): JSX.Element => {
    const {
        rows,
        classes,
        mode,
        moduleName,
        rbacList,
        // onChangeModuleName,
        roleId,
        onUpdateChange,
        onNavigateSideMenu,
    } = props;
    const [isAddedModalRole, setIsAddedModalRole] = React.useState(false)
    const [moduleValue, setModuleValue] = React.useState(0);
    const [isModuleModalOpen, setIsModuleModalOpen] = React.useState(false);
    const [moduleLabel, setModuleLabel] = React.useState<any>(null);
    const [effectivityDate, setEffectivityDate] = React.useState('');
    const [expiryDate, setExpiryDate] = React.useState('');
    const [description, setDescription] = React.useState(null);
    const [roleName, setRoleName] = React.useState('');
    const [isModalConfirmChangeModule, setIsModalConfirmChangeModule] = React.useState(false)
    const [newRbacList, setNewRbacList] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [defaultFilters, setDefaultFilters] = React.useState([]);
    const [isModalDeleteSuccessOpen, setIsModalDeleteSuccessOpen] = React.useState(false)
    const [isModalCannotEditOpen, setIsModalCannotEditOpen] = React.useState(false)
    const [isProcessMaker, setIsProcessMaker] = React.useState(false)
    console.log(mode, rbacList, newRbacList.length, 'policies')
    const moduleItems = [
        {
            name: 'User Management',
            value: '0'
        },
        {
            name: 'Membership',
            value: '1'
        },
        {
            name: 'Underwriting',
            value: '2'
        },
        {
            name: 'Customer Care',
            value: '3'
        },
        {
            name: 'Claims',
            value: '4',
        },
        // {
        //     name: 'Billing',
        //     value: '5',
        // },
    ]

    const columns: any = [
        {
            name: 'operation',
            title: 'Object and Operations',
        },
        {
            name: 'policy',
            title: 'Policies',
        },
        {
            name: 'policy_id',
            title: 'Policy ID',
        },
        {
            name: 'name',
            title: 'Name'
        }
    ]

    // React.useEffect(() => {
    //     console.log('policies useeffect')
    // },[])

    const handleCannotEditRoleModalClose = () => {
        setIsModalCannotEditOpen(false)
    }

    const handleDeleteSuccessRoleModalClose = () => {
        setIsModalDeleteSuccessOpen(false)
    }

    const handleRemoveRoleModalClose = () => {

    }

    const handleContinueRole = () => {
        let moduleName = moduleItems[Number(moduleValue)].name;
        setIsModalConfirmChangeModule(false);
        setModuleLabel(moduleName);
        // ononChangeModuleName(moduleName);
    }

    const handleOnCheckRow = (row) => {
        setLoading(true);

        let rbac = rbacList;
        if (newRbacList.length === 0) {
            setNewRbacList(rbacList);
        } else {
            rbac = newRbacList;
        }
        if (rbac.length > 0) {
            rbac.map((data: any) => {
                if (data._id === row._id) {
                    data['checked'] = !data['checked'];
                }
            })
            setNewRbacList(rbac);
        }
        setLoading(false)
    }

    const handleOnCheckGroup = (group) => {
        setLoading(true);
        let rbac = rbacList;
        let countChecked = 0;

        if (newRbacList.length === 0) {
            setNewRbacList(rbacList)
        }
        if (rbac.length > 0) {
            rbac.map((data: any) => {
                if (data.name == group && data.checked === true) {
                    countChecked++;
                }
            })
        }
        if (countChecked > 0) {
            rbac.map((data: any) => {
                if (data.name == group) {
                    data['checked'] = false;
                }
            })
        } else {
            rbac.map((data: any) => {
                if (data.name == group) {
                    data['checked'] = true;
                }
            })
        }
        setNewRbacList(rbac);
        setTimeout(() => {
            setLoading(false);
        }, 500)
    }

    const handleUpdateCheck = () => {
        var rbac: any[] = [];
        const _rbacList = newRbacList.length > 0 ? newRbacList : rbacList;

        rbac = _rbacList.filter((data: any) => {
            return data.checked;
        })

        var data: RolesRbacData = {
            module: moduleLabel !== null ? moduleLabel : moduleName,
            name: roleName !== '' ? roleName : rows.name,
            role_id: roleId !== '' ? roleId : rows.role_id,
            effectivity_date: moment(effectivityDate !== '' ? effectivityDate : new Date()).toISOString(),
            description: description !== null ? description : rows.description,
            status: 'active',
            is_processmaker: isProcessMaker ? isProcessMaker : rows.is_processMaker ? rows.is_processmaker : false,
            rbac: rbac.length > 0 ? rbac : rows.rbac,
        }

        if (mode === 'edit') {
            data['_id'] = rows._id
            data['status'] = rows.status
        }

        if (expiryDate !== '') {
            data['expiry_date'] = moment(expiryDate).toISOString();
        }

        console.log(data);

        onUpdateChange(data);
    }

    const handleOnFilterChange = (filter) => {
        setDefaultFilters(filter);
    }

    const viewRoleBody = () => {
        return (
            <>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE ID</InputLabel>
                    <Typography><b>{rows && rows.role_id ? rows.role_id : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">MODULE</InputLabel>
                    <Typography><b>{rows && rows.module ? rows.module : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">DESCRIPTION</InputLabel>
                    <Typography><b>{rows && rows.description ? rows.description : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE NAME</InputLabel>
                    <Typography><b>{rows && rows.name ? rows.name : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="status">STATUS</InputLabel>
                    <Typography><b>{rows && rows.status ? rows.status : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">EFFECTIVITY DATE</InputLabel>
                    <Typography><b>{rows && rows.effectivity_date ? moment(rows.effectivity_date).format('MMMM DD, YYYY') : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">EXPIRY DATE</InputLabel>
                    <Typography><b>{rows && rows.expiry_date ? moment(rows.expiry_date).format('MMMM DD, YYYY') : '-'}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">ATTACHED GROUP/S</InputLabel>
                    <Typography>
                        {
                            rows && rows.associate_groups ? rows.associate_groups.length > 0 && rows.associate_groups.map((groups) => (
                                <b>{groups}</b>
                            )) : '-'
                        }
                    </Typography>
                    <Link onClick={() => {
                        onNavigateSideMenu && onNavigateSideMenu('Groups And Users')
                    }}>+ Add Group</Link>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">LAST UPDATE</InputLabel>
                    <Typography><b>{rows && rows.effectivity_date ? moment(rows.effectivity_date).format('MMMM DD, YYYY') : '-'}</b></Typography>
                </Grid>
            </>
        )
    }

    const addRoleBody = () => {
        return (
            <>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">MODULE</InputLabel>
                    <Typography><b>{moduleLabel !== null ? moduleLabel : moduleName}</b></Typography>
                    <Link onClick={() => { setIsModuleModalOpen(true) }}>Change Module</Link>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE ID</InputLabel>
                    <Typography><b>{roleId}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">DESCRIPTION</InputLabel>
                    {/* <TextareaAutosize></TextareaAutosize> */}
                    <BasicTextField
                        onBlur={(e: any) => {
                            console.log(e.target.value)
                            setDescription(e.target.value);
                            handleUpdateCheck();
                        }}
                    ></BasicTextField>
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE NAME</InputLabel>
                    <BasicTextField
                        onBlur={(e: any) => {
                            setRoleName(e.target.value);
                            handleUpdateCheck();
                        }}
                    ></BasicTextField>
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel}>IS A PROCESSMAKER PROCESS MAKER ROLE</InputLabel>
                    <Checkbox
                        checked={isProcessMaker !== null ? isProcessMaker : false}
                        onChange={(e: any) => {
                            // console.log(e.target.checked)
                            setIsProcessMaker(e.target.checked)
                            handleUpdateCheck();
                        }}
                    ></Checkbox>
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">EFFECTIVITY DATE</InputLabel>
                    <KeyboardDatePicker
                        views={["year", "date", "month"]}
                        value={effectivityDate !== '' ? effectivityDate : new Date()}
                        format={'MMM DD, YYYY'}
                        style={{ width: "100%", marginTop: 10 }}
                        onChange={(e: any) => {
                            setEffectivityDate(e)
                            handleUpdateCheck();
                        }}
                        inputVariant={'outlined'}
                    // disableFuture
                    />
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">EXPIRY DATE (If applicable)</InputLabel>
                    <KeyboardDatePicker
                        views={["year", "date", "month"]}
                        value={expiryDate !== '' ? expiryDate : null}
                        style={{ width: "100%", marginTop: 10 }}
                        format={'MMM DD, YYYY'}
                        onChange={(e: any) => {
                            setExpiryDate(e)
                            handleUpdateCheck();
                        }}
                        inputVariant={'outlined'}
                    // disableFuture
                    />
                </Grid>
            </>
        )
    }

    const editRoleBody = () => {
        return (
            <>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">MODULE</InputLabel>
                    <Typography><b>{moduleLabel !== null ? moduleLabel : rows.module}</b></Typography>
                    {/* <Link onClick={() => { setIsModuleModalOpen(true) }}>Change Module</Link> */}
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE ID</InputLabel>
                    <Typography><b>{moduleLabel !== null ? roleId : rows.role_id}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">DESCRIPTION</InputLabel>
                    {/* <TextareaAutosize></TextareaAutosize> */}
                    <BasicTextField
                        value={description !== null ? description : rows.description}
                        onChange={(e: any) => {
                            setDescription(e.target.value)
                        }}
                        onBlur={() => {
                            handleUpdateCheck()
                        }}
                        disabled={rows.is_processMaker ? true : false}
                    ></BasicTextField>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE NAME</InputLabel>
                    <Typography><b>{rows && rows.name ? rows.name : '-'}</b></Typography>
                    {/* <BasicTextField
                        value={roleName !== '' ? roleName : rows.name}
                        onChange={(e: any) => {
                            setRoleName(e.target.value);
                            handleUpdateCheck()
                        }}
                        disabled={true}
                    ></BasicTextField> */}
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel}>IS A PROCESSMAKER PROCESS MAKER ROLE</InputLabel>
                    <Checkbox
                        checked={isProcessMaker !== null ? isProcessMaker : rows.is_processMaker}
                        onChange={(e: any) => {
                            setIsProcessMaker(e.target.checked)
                            handleUpdateCheck()
                        }}
                        disabled={false}
                    ></Checkbox>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">STATUS</InputLabel>
                    <Typography><b>{rows && rows.status}</b></Typography>
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">EFFECTIVITY DATE</InputLabel>
                    <KeyboardDatePicker
                        views={["year", "date", "month"]}
                        value={effectivityDate !== '' ? effectivityDate : moment(rows.effectivity_date)}
                        format={'MMM DD, YYYY'}
                        style={{ width: "100%", marginTop: 10 }}
                        onChange={(e: any) => {
                            setEffectivityDate(e)
                            handleUpdateCheck()
                        }}
                        disabled={rows.is_processMaker ? true : false}
                        inputVariant={'outlined'}
                    // disableFuture
                    />
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">EXPIRY DATE (If applicable)</InputLabel>
                    <KeyboardDatePicker
                        views={["year", "date", "month"]}
                        value={expiryDate !== '' ? expiryDate : rows.expiry_date ? moment(rows.epiry_date) : null}
                        style={{ width: "100%", marginTop: 10 }}
                        format={'MMM DD, YYYY'}
                        onChange={(e: any) => {
                            setExpiryDate(e)
                            handleUpdateCheck()
                        }}
                        disabled={rows.is_processMaker ? true : false}
                        inputVariant={'outlined'}
                    // disableFuture
                    />
                </Grid>
            </>
        )
    }

    return (
        <>
            <Grid container xs={12} spacing={4}>
                {
                    mode === 'view' ? (
                        viewRoleBody()
                    ) : mode === 'edit' ? (
                        editRoleBody()
                    ) : mode === 'add' ? (
                        addRoleBody()
                    ) : null
                }
                <Grid item xs={12}>

                    <ReactSearch
                        onSearch={() => console.log('on search')}
                        placeholder={'Search policies'}
                    />
                    {
                        <ReactTable
                            rows={newRbacList.length > 0 && mode !== 'view' ? newRbacList : rbacList}
                            columns={columns}
                            enableGrouping={true}
                            groupColumnName={'name'}
                            viewOnly={mode === 'view' ? true : false}
                            enableOpenGroupTotalChecked={mode === 'view' ? true : false}
                            message={'There are no policies to show yet.'}
                            enableSelection={mode !== 'view' ? true : false}
                            loading={loading}
                            onCheckRow={(row) => {
                                handleOnCheckRow(row)
                                setTimeout(() => {
                                    handleUpdateCheck()
                                }, 500)
                            }}
                            defaultFilters={defaultFilters}
                            onFilterChange={handleOnFilterChange}
                            onSelectGroup={(group) => {
                                handleOnCheckGroup(group)
                                setTimeout(() => {
                                    handleUpdateCheck()
                                }, 500)
                            }}
                        />
                    }
                </Grid>
            </Grid>
            <ModalComponent
                isModalOpen={isAddedModalRole}
                onClose={handleRemoveRoleModalClose}
                message={'New Role has been added successfully.'}
                title={'Added New Role'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setIsAddedModalRole(false) }} className={classes.button} variant={'contained'} color={'primary'}>Okay</Button>
                        </Grid>
                    </Grid>
                }
            />
            <ModalComponent
                isModalOpen={isModuleModalOpen}
                onClose={() => {
                    setIsModuleModalOpen(false)
                }}
                message={'Select a module for the role you will create:'}
                title={'Choose a Module'}
                buttons={
                    <Grid container justify="center" alignItems="center" style={{ textAlign: 'center' }}>
                        <Grid item xs={6}>
                            <Button
                                onClick={() => {
                                    setIsModuleModalOpen(false)
                                }}
                                variant={'outlined'}
                                color={'secondary'}
                                className={classes.button}
                            >Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button variant={'contained'} color={'primary'}
                                className={classes.button}
                                onClick={() => {
                                    setIsModalConfirmChangeModule(true)
                                    setIsModuleModalOpen(false)
                                }}>Continue</Button>
                        </Grid>
                    </Grid>
                }
            >
                <div style={{ width: '90%', margin: 'auto' }}>
                    <BasicSelect
                        menuItems={moduleItems}
                        width={'100%'}
                        value={moduleValue}
                        onChange={(e: any) => {
                            setModuleValue(e.target.value)
                        }}
                    />
                </div>
            </ModalComponent>
            <ModalComponent
                isModalOpen={isModalConfirmChangeModule}
                onClose={() => {
                    setIsModalConfirmChangeModule(false);
                }}
                message={'All progress made will be lost. Continue to change the module?'}
                title={'Confirm Change Module'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setIsModalConfirmChangeModule(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button className={classes.button} variant={'contained'} color={'primary'} onClick={() => handleContinueRole()}>Continue</Button>
                        </Grid>
                    </Grid>
                }
            />

            <ModalComponent
                isModalOpen={isModalDeleteSuccessOpen}
                onClose={handleDeleteSuccessRoleModalClose}
                message={'New role has been deleted.'}
                title={'Delete Success'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item >
                            <Button
                                onClick={() => {
                                    setIsModalDeleteSuccessOpen(false)
                                }}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />


            <ModalComponent
                isModalOpen={isModalCannotEditOpen}
                onClose={handleCannotEditRoleModalClose}
                message={'You do not have an assigned role to your account to perform this action.'}
                title={'Cannot Edit Role'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item>
                            <Button
                                onClick={() => {
                                    setIsModalCannotEditOpen(false)
                                }}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />
        </>
    )
}